export default class ProjectData {
    id: string;
    name: string;
    html: string;
    videoLink: string; 
    github: string;
    itch: string;
    accentColor: string; 
    tags?: string[];
    proud: string;
    genre: string;
    engine: string;
    teamSize: string;
    duration: string;

    constructor(id: string, name: string, html: string, videoLink: string, github: string, itch: string, accentColor = "#000000", tags: string[] = [], proud = "", genre = "", engine = "", teamSize = "", duration = ""){
        this.id = id;
        this.name = name;
        this.html = html;
        this.videoLink = videoLink;
        this.github = github;
        this.itch = itch;
        this.accentColor = accentColor;
        this.tags = tags;
        this.proud = proud;
        this.genre = genre;
        this.engine = engine;
        this.teamSize = teamSize;
        this.duration = duration;
    }
}
