
import Vue from "vue";
import ProjectDetailsOverlay from "@/components/ProjectDetailsOverlay.vue";
import ProjectData from "@/data/ProjectData.ts";

export default Vue.extend({
  name: "ProjectsList",
  components: {
    ProjectDetailsOverlay,
  },
  props: {
    projects: Array
  },
  data: function () {
    return {
      showPopup: false,
      popupTitle: "",
      popupColor: "",
      popupContent: "",
      popupGithub: "",
      popupItch: "",
      popupTags: [] as string[],
      popupProud: "",
      popupGenre: "",
      popupEngine: "",
      popupTeamSize: "",
      popupDuration: ""
    };
  },
  methods: {
    showDetails: function (item: ProjectData) {
      // if (event) {
      //   alert(event.target);
      // }
      this.popupTitle = item.name;
      this.popupColor = item.accentColor;
      this.popupContent = item.html;
      this.popupGithub = item.github;
      this.popupItch = item.itch;
      this.popupTags = item.tags ?? [];
      this.popupProud = item.proud;
      this.popupGenre = item.genre;
      this.popupEngine = item.engine;
      this.popupTeamSize = item.teamSize;
      this.popupDuration = item.duration;
      this.showPopup = true;
      window.scrollTo(0,0);
      this.$router.push(`/game-projects/${item.id}`)
    },
    closePopup() {
    this.showPopup = false
    this.$router.push('/game-projects')
  }
  },
});
