
import Vue from "vue";
import ProjectsList from "@/components/ProjectsList.vue";
import gameProjectsData from "@/data/GameProjectsData.ts";

export default Vue.extend({
  name: "GameProjects",
  components: {
    ProjectsList,
  },
  data: function () {
    return {
      projects: gameProjectsData,
    };
  },
  mounted() {
    this.openFromRoute()
  },

  watch: {
    '$route.params.id'() {
      this.openFromRoute()
    }
  },

  methods: {
    openFromRoute() {
      const id = String(this.$route.params.id)
      if (!id) return

      const project = this.projects.find(p => p.id === id)
      if (!project) return

      // call ProjectsList method
      ;(this.$refs.projectsList as any).showDetails(project)
    }
  }
});

